import axios from 'axios'

export const fetchPorts = async () => {
    const res = await axios.get('http://localhost:8000/get_ports')
    return res.data
}

export const handleConnectDevice = async (com, rate) => {
    const res = await axios.post('http://localhost:8000/handle_connect', { com, rate })
    return res.data
}

export const handlePrint = async ({
    data,
    address
}) => {
    const res = await axios.post('http://localhost:8000/print', { data, address })
    return res.data
}