
import React, { useEffect } from 'react';
import './App.css';
import Header from './Header';

import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Select from '@mui/material/Select';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import TextField from '@mui/material/TextField';
import Stack from '@mui/material/Stack';
import Switch from '@mui/material/Switch';
import FormControlLabel from '@mui/material/FormControlLabel';
import LinearProgress from '@mui/material/LinearProgress';
import Snackbar from '@mui/material/Snackbar';
import { Alert } from '@mui/material';

import { mkConfig, generateCsv, download } from "export-to-csv";

import { useState } from 'react';

//import Components
import DemoPaperOne from './Components/DemoPaperOne';
import DemoPaperTwo from './Components/DemoPaperTwo';

//import api utils
import { fetchPorts, handleConnectDevice, handlePrint } from './utils'

const csvConfig = mkConfig({ useKeysAsHeaders: true, filename: 'device_info' });

function App() {
  const [com, setCom] = useState([]);
  const [selectedCom, setSelectedCom] = React.useState("");
  const [selectedRate, setSelectedRate] = React.useState("");
  const [selected, setSelected] = React.useState(true);
  const [show, setShow] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [devEUI, setDevEUIg] = useState("")
  const [appKey, setAppKey] = useState("")
  const [bleMac, setBleMac] = useState("")
  const [appEUI, setAppEUI] = useState("")
  const [error, setError] = useState("")
  const [ipAddress, setIpAddress] = useState("")

  const handleClickPrint = async () => {
    const data = {
      devEUI,
      appKey,
      bleMac,
      appEUI
    }
    const res = await handlePrint({
      data,
      address: ipAddress
    })
    console.log(res)
    if (!res.success) {
      setError(res.error)
      return
    }
  }

  const handleChange = (event) => {
    setSelectedCom(event.target.value);
  };

  const handleConnect = () => {
    setIsLoading(true)

    handleConnectDevice(selectedCom, selectedRate)
      .then(res => {
        if (!res.success) {
          setError(res.error)
          setIsLoading(false)
          return
        }
        setDevEUIg(res.devEUI)
        setAppKey(res.appKey)
        setBleMac(res.bleMac)
        setAppEUI(res.appEUI)
        setIsLoading(false)
        setError("")
        setShow(true)
      })
  }

  useEffect(() => {
    fetchPorts().then(_coms => {
      setCom([..._coms])
    })
  }, []);

  return (
    <div className='App'>
      <Snackbar
        open={error ? true : false}
        autoHideDuration={6000}
        onClose={() => setError("")}
        // message={error}
        // action={action}
        anchorOrigin={{ vertical: "top", horizontal: "right" }}
      >
        <Alert
          onClose={() => setError("")}
          severity="error"
          variant="filled"
          sx={{ width: '100%' }}
        >
          {error}
        </Alert>
      </Snackbar>
      <Header />
      <Stack direction="row" spacing={2} sx={{ display: 'flex', justifyContent: 'center', alignContent: 'cetnter', mt: 10 }}>
        <DemoPaperOne elevation={5} sx={{ width: "40%" }}>
          <Typography variant="h4" component="div" sx={{ p: 2, textAlign: 'center' }}>
            Please Select Some Options
          </Typography>
          <FormControl
            fullWidth
            sx={{ mt: 2 }}
          >
            <InputLabel id="demo-simple-select-label">Com Port</InputLabel>
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              // value={selectedCom}
              value={selectedCom}
              label="Com Port"
              onChange={handleChange}
            >
              {
                com && com.map((c) =>
                  <MenuItem key={c.friendlyName} value={c.path}>{c.path}</MenuItem>)
              }
            </Select>
          </FormControl>
          <FormControl fullWidth sx={{ mt: 2 }}>
            <InputLabel id="demo-simple-select-label2" >Raud Rate</InputLabel>
            <Select
              labelId="demo-simple-select-label2"
              id="demo-simple-select2"
              value={selectedRate}
              label="Baud Rate"
              onChange={(e) => setSelectedRate(e.target.value)}
            >

              <MenuItem value="9600">9600</MenuItem>
              <MenuItem value="57600">57600</MenuItem>
              <MenuItem value="115200">115200</MenuItem>
            </Select>
            <FormControlLabel sx={{ mt: 2 }} control={<Switch checked={selected} onChange={() => setSelected(!selected)} />} label="Auto Print" />

            <Button
              onClick={handleConnect}
              sx={{ mt: 2, p: 1.3 }}
              variant='contained'
            >
              Connect With Device
            </Button>
            {isLoading && <LinearProgress />}

          </FormControl>

        </DemoPaperOne>

        {show && <DemoPaperTwo elevation={5} sx={{ width: "40%" }}>
          <Typography variant="h4" component="div" sx={{ p: 2, textAlign: 'center' }}>
            Print Options
          </Typography>
          <FormControl
            fullWidth
          >
            <TextField
              sx={{ mt: 2 }}
              id="outlined-number"
              label="Ip Address"
              type="text"
              onChange={(e) => setIpAddress(e.target.value)}
              value={ipAddress}
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField id="outlined-DevEUI" label="DevEUI" sx={{ mt: 2 }} variant="outlined" value={devEUI} />
            <TextField id="outlined-AppKEY" label="AppKEY" sx={{ mt: 2 }} variant="outlined" value={appKey} />
            <TextField id="outlined-basic" label="BLE MAC" sx={{ mt: 2 }} variant="outlined" value={bleMac} />
            <TextField id="outlined-AppEUI" label="AppEUI" sx={{ mt: 2 }} variant="outlined" value={appEUI} />
            <Stack direction={"row"} spacing={2} sx={{ mt: 2, justifyContent: "space-between" }} >
              {
                !selected
                  ?
                  <>
                    <Button
                      onClick={handleConnect}
                      sx={{ mt: 2, p: 1.3, width: "100%" }}
                      variant='contained'
                    >
                      Save as CSV
                    </Button>
                  </> :
                  <>
                    <Button
                      onClick={handleClickPrint}
                      sx={{ mt: 2, p: 1.3, width: "50%" }}
                      variant='contained'
                    >
                      Print
                    </Button>
                    <Button
                      onClick={() => {
                        const csv = generateCsv(csvConfig)([
                          {
                            devEUI,
                            appKey,
                            bleMac,
                            appEUI
                          }
                        ]);
                        download(csvConfig)(csv)
                      }}
                      sx={{ mt: 2, p: 1.3, width: "50%" }}
                      variant='contained'
                    >
                      Save as CSV
                    </Button>
                  </>
              }
            </Stack>
          </FormControl>

        </DemoPaperTwo>}

      </Stack >
    </div >
  );
}

export default App;
